
//
.CommentInput_contain {
    background-color: var(--md-bg-fb);
    border-radius: 15px;
}
// ..
.CommentInput_input {
    flex-basis: 1rem;
    flex-grow: 1;

    padding-left: 0.5rem;
    overflow: hidden;
}
// ...
.CommentInput_textarea{
    padding: 0;
}

// ..
.CommentInput_files {
    align-self: flex-end;
    padding-right: 0.25rem;
}
// ...
// .CommentInput_files-row{
//     .InputFile {
//         width: 1.25rem;
//         height: 1.25rem;
//     }
// }
// ....
// .CommentInput_files-col {
//     padding: 0.2rem;
// }
// ....
.CommentInput__send {
    display: none;
}


// preview
.CommentInput_preview {
    max-width: 100%;
    overflow-x: auto;
    .ImgVidPreview {
        display: flex;
        .ImgVidPreview__elm {
            position: relative;
            margin: 0.25rem;
        }
    }
}

//
@media (max-width: 600px) {
    // ...
    .CommentInput_files-row {
        .IconsInput {
            width: 0.8rem;
            height: 0.8rem;
        }
    }
    // ....
    .CommentInput__send {
        display: block;
    }
}
